*,
*::before,
*::after {
    box-sizing: border-box;
}

.flex {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}

.--just-space {
    justify-content: space-between;
}

.--just-center {
    justify-content: center;
}

.--just-end {
    justify-content: flex-end;
}

.--align-str {
    align-items: stretch;
}

.--align-center {
    align-items: center;
}

.--align-end {
    align-items: flex-end;
}

.--dir-col {
    flex-direction: column;
}
.--border-radius {
    border-radius: 140px 0;
}

.--accent-bg {
    background-color: $accent;
    color: #ffffff;
}

html {
    font-size: 16px;
}

main {
    scroll-behavior: smooth;
}

html,
body {
    min-height: 100%;
    position: relative;
}

body {
    font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

.title {
    @extend %poppins-medium;
    font-size: 72px;
    line-height: 78px;
    color: $title;
    margin-bottom: 25px;
}
.title--small {
    @extend %poppins-medium;
    font-size: 48px;
    line-height: 57px;
    color: $title;
    margin-bottom: 25px;
}

.sup-title {
    @extend %poppins-semibold;
    font-size: 20px;
    line-height: 28px;
    color: $text-color;
    margin-bottom: 8px;
}

.container {
    max-width: 1400px;
    padding: 0 15px;
    margin: 0 auto;
}

.btn {
    @extend %opensans-semibold;
    font-size: 18px;
    line-height: 78px;
    background-color: $btn-color;
    border-radius: 20px 0;
    color: #ffffff;
    padding: 9px 28px;
    text-transform: uppercase;
}

.text {
    @extend %opensans-regular;
    font-size: 18px;
    line-height: 30px;
    color: $text-color;
}

@media(max-width: 1367px) {
    .--border-radius {
        border-radius: none;
    }
}
@media(max-width: 769px) {
    .btn {
        display: flex;
        justify-content: center;
        margin: 0 auto;
        max-width: 40%;
        text-align: center;
        padding: 0;
        font-size: 15px;
        line-height: 50px;
    }
}

@media(max-width: 640px) {
    .btn {
        max-width: 65%;
    }
    .title {
        font-size: 60px;
        line-height: 68px;
        letter-spacing: 2px;
    }
}

@media (max-width: 415px) {
    .btn {
        max-width: 85%;
    }
}

@media(max-width: 376px) {
    .title {
        font-size: 40px;
        line-height: 48px;
        letter-spacing: 1px;
    }
    .title--small {
        font-size: 38px;
        line-height: 47px;
    }
}