.menu {
    display: flex;
    &__menu-burger {
        display: none;
    }
    &__wrapper {
        position: fixed;
        left: 0;
        right: 0;
        z-index: 50;
        background-color: #ffffff;
        transition: all .3s ease-in-out;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 50px;
        @extend %opensans-semibold;
    }
    &__list {
        display: flex;
        align-items: center;
    }
    &__item {
        text-transform: uppercase;
    }
    &__item+.menu__item {
        margin-left: 44px;
    }
    &__link--active {
        color: $text-color;
    }
    &__link:hover {
        color: $text-color;
    }
}

.top__btn {
    background-color: $accent;
    margin-left: 65px;
    color: #ffffff;
    padding: 5px 20px;
    border-radius: 15px 0;
}

.menu--bg {
    padding: 20px 50px;
    background-color: $bg-color;
}

@media(max-width: 1120px) {
    .menu{
        &__item+.menu__item {
            margin-left: 30px;
        }
    }
    .top__btn {
        margin-left: 0;
    }
}

@media(max-width: 990px) {
    .header {
        position: fixed;
        top: 0;
	    left: 0;
        right: 0;
	    z-index: 20;
        background-color: #fff;
       
    }
    .menu {
        display: block;
	    position: absolute;
        top: 100%;
        left: -120%;
        width: 100vw;
        transition: all 0.4s ease;
        background-color: #fff;
        padding-right: 50px;
        z-index: -1;
        &__link {
            display: block;
            text-align: center;
        }
        &__list {
            display: block;
        }
        &__item {
            margin: 40px 0 40px 33px;
        }
        &__wrapper {
            padding: 20px 15px;
        }
    }
    .logo {
        &__link {
            width: 120px;
        }
    }
    .menu.open-menu {
      left: -15px;
    }
    .header__menu-burger {
        display: block;
        position: relative;
        width: 40px;
        height: 35px;
        display: block;
        cursor: pointer;
    }
    .header__menu-burger span, .header__menu-burger:after, .header__menu-burger:before{
        height: 3px;
        width: 100%;
        position: absolute;
        background: #515758;
        margin: 0 auto;
    }
    .header__menu-burger span{
        top: 16px;
    }
    .header__menu-burger:after, .header__menu-burger:before{
        content: '';
    }
    .header__menu-burger:after{
        bottom: 5px;
    }
    .header__menu-burger:before{
        top: 5px;
    }
    .header__menu-burger.open-menu span {
        opacity:0; 
        transition: 0.5s; 
    }
    .header__menu-burger.open-menu:before {
        transform: rotate(38deg);
        top: 16px;
        transition: 0.4s;
    }
    .header__menu-burger.open-menu:after {
        transform: rotate(-38deg);
        bottom: 16px;
        transition: 0.4s;
    }
}

@media(max-width: 767px) {
    .menu.open-menu {
        left: -15px;
    }
    .menu {
        height: auto;
        &__link {
            text-align: center;
        }
    }
    .logo {
        &__link {
            display: none;
        }
    }
}