.contact {
    padding: 200px 0 100px;
    position: relative;
    &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        background-image: url("../svg/dots-bg.svg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        width: 213px;
        height: 154px;
    }
    &__wrapper {
        display: flex;
    }
    &__item {
        max-width: 606px;
        margin-top: 100px;
    }
    &__title {
        margin-bottom: 23px;
        font-weight: 600;
    }
    &__text {
        margin-bottom: 46px;
    }
    &__image-box {
        display: inline-block;
        max-width: 717px;
        position: relative;
        border: solid 2px $accent;
        border-radius: 166px 0;
        margin-left: 0;
        padding-bottom: 0;
        margin-left: 78px;
    }
    &__link-image{
        display: block;
        
    }
    &__image {
        max-width: 100%;
        object-fit: cover;
        margin-left: -38px;
        margin-top: -38px;
        margin-bottom: 31px;
    }
}

@media(max-width: 1367px) {
    .contact {
        padding: 50px 0;
        &__wrapper {
            flex-direction: column-reverse;
        }
        &__item {
            max-width: 100%;
            margin-top: 70px;
        }
        &__image-box {
            display: block;
            max-width: 100%;
            border: none;
            border-radius: 166px 0;
            margin-left: 0;
        }
        &__image {
            width: 100%;
            max-width: 100%;
            margin-left: 0;
            margin-top: 0;
            margin-bottom: 0;
            height: 500px;
        }
    }
    .--border-radius {
        border-radius: 0;
    }
}

@media(max-width: 415px) {
    .contact {
        &__image {
            height: 330px;
        }
        &__item {
            margin-top: 50px;
        }
    }
}
@media(max-width: 376px) {
    .contact {
        &__text {
            margin-bottom: 18px;
        }
    }
}

