.reviews {
  padding: 100px 0;
  text-align: center;
}

.reviews-slider{
  padding: 100px 0;
  &__item {
    max-width: 571px;
    box-shadow: 7px 7px 30px 5px rgba(66, 96, 84, 0.15);
    border-radius: 70px 0px;
    text-align: left;
  }
  &__wrapper {
    display: flex;
    padding: 40px 30px 60px 100px;
  }
  &__avatar {
    width: 130px;
    height: 130px;
    border-radius: 35px 0px;
    object-fit: cover;
    margin-left: -156px;
    margin-right: 33px;
   
  }
  &__text-box {
    @extend %opensans-regular;
  }
  &__item-name {
    position: relative;
    margin-bottom: 8px;
    &::after {
      content: '';
      position: absolute;
      width: 38px;
      height: 38px;
      background-image: url('../img/quotes.svg');
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      right: 0;
      top: 0;
    }
  }
  &__item-text {
    line-height: 28px;
  }
  .slick-track {
    height: auto;
    padding-bottom: 15px;
  }
  .slick-next,
  .slick-prev {
      position: absolute;
      top: 0px;
      right: 60px;
      width: 30px;
      height: 30px;
      background-color: $btn-color;
      display: flex;
      justify-content: center;
      align-items: center;
      border: none;
      border-radius: 50%;
      box-shadow: -1px 3px 10px rgba(0, 0, 0, 0.3);
      outline: transparent;
      z-index: 5;
      cursor: pointer;
      path {
          stroke: #ffffff;
      }
  }
  .slick-prev{
      right: 140px;
  }
  .slick-disabled {
      background-color: white;
      box-shadow: -1px 3px 10px rgba(0, 0, 0, 0.3);
      path {
          stroke: $text-color;
      }

  }
  .slick-dots {
    display: flex;
    justify-content: center;
    margin-top: 50px;
    li {
      padding: 0 15px;
      line-height: 0;
      button {
        cursor: pointer;
        padding: 0;
        font-size: 0;
        background-color: transparent;
        width: 7px;
        height: 7px;
        border-radius: 50%;
        outline-color: transparent;
        border: 1px solid transparent;
        position: relative;
        &::before {
          content: '';
          position: absolute;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          background-color: $btn-color;
          opacity: .5;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          transition: all .4s;
        }
      }
    }    
    .slick-active {
      button {
        &::before {
          opacity: 1;
          background-color: $btn-color;
          width: 8px;
          height: 8px;
        }
      }
    }
  }
}
.client-name {
  @extend %opensans-bold;
  font-size: 24px;
  line-height: 34px;
  color: $title;
}
.client-prof {
  font-size: 16px;
  line-height: 22px;
  color: $text-color;
  padding-left: 7px;
  position: relative;
  &::after {
    content: '/';
    position: absolute;
    left: 0;
    top: 0;
  }
}
.slick-slide {
  margin: 15px;
  
}
.slick-list{
  padding: 0px;
  margin: 0;
  height: auto;
}

@media(max-width: 1420px) {
  .reviews-slider {
    padding: 50px 0;
    .slick-next,
    .slick-prev {
      right: 50%;
      left: 50%;
    }
    .slick-next {
      transform: translateX(30px);
    }
    .slick-prev{
      transform: translateX(-30px);
    }
  }
}

@media(max-width: 990px) {
  .reviews-slider {
    padding: 0;
    .slick-dots {
      margin-top: 10px;
    }
  }
}

@media(max-width: 769px) {
  .reviews-slider {
    &__avatar {
      margin-left: 0px;
      margin: 0 auto 10px;

    }
    &__item-name {
      &::after {
        display: none;
      }
    }
    &__wrapper {
      flex-direction: column;
      padding: 30px 20px; 
    }
    &__item-text {
      font-size: 14px;
      line-height: 18px;
    }
    .client-prof {
      display: block;
    }
  }  
}

@media(max-width: 415px) {
  .reviews {
    padding: 100px 0 50px;
  }
  .client-name {
    font-size: 18px;
    line-height: 25px;
  }
  .client-prof {
    font-size: 14px;
    line-height: 17px;
  }
}