.blog {
  padding: 100px 0 120px 0;
  background-color: $bg-color;
  &__sup-title {
    text-align: center;
  }
  &__title {
    margin-bottom: 90px;
    text-align: center;
  }
  &__items {
    display: flex;
    justify-content: space-between;
  }
  &__image-link {
    display: block;
    width: 554px;
    height: 387px;
    border: 2px solid #B0855B;
    border-radius: 100px 0px;
    margin-left: 16px;
    margin-bottom: 40px;
  }
  &__image {
    display: block;
    width: 100%;
    height: auto;
    border-radius: 100px 0px;
    margin-left: -16px;
    margin-top: -16px;
  }
  &__item-title {
    @extend %opensans-semibold;
    font-size: 30px;
    line-height: 30px;
  }
  &__text {
    line-height: 28px;
    margin-bottom: 40px;
  }
}

@media(max-width: 1367px) {
  .blog {
    padding: 50px 0;
    &__title {
      margin-bottom: 50px;
    }
    &__items {
      flex-direction: column;
    }
    &__item {
      margin-bottom: 70px;
    }
    &__item-title {
      margin-bottom: 10px;
    }
    &__image-link {
      width: 100%;
      height: auto;
      border: none;
      border-radius: none;
      margin: 0;
      margin-bottom: 30px;
    }
    &__image {
      border-radius: 0;
      margin: 0;
    }
    &__text {
      margin-bottom: 30px;
    }
  }
}

@media (max-width: 769px) {
  .blog {
    &__link {
      display: flex;
      justify-content: center;
      margin: 0 auto;
      max-width: 40%;
      text-align: center;
      padding: 0;
      font-size: 15px;
      line-height: 50px;

    }
  }
}

@media (max-width: 640px) {
  .blog {
    &__text {
      margin-bottom: 30px;
    }
    &__link {
      max-width: 65%;
    }
  }
}

@media (max-width: 415px) {
  .blog {
    &__text {
      margin-bottom: 30px;
    }
    &__link {
      max-width: 85%;
    }
  }
}