.advances {
  background-color: $bg-color;
  padding: 100px 0;
  &__title{
    text-align: center;
    margin-bottom: 87px;
  }
  &__items {
    display: flex;
    justify-content: space-between;
    height: 278px;
  }
  &__item {
    background-color: #fff;
    box-shadow: 0px 13px 16px rgba(0, 0, 0, 0.1);
    border-radius: 40px 0px;
    padding: 50px 0;
    @extend %opensans-bold;
    color: $title;
    text-align: center;
    display: flex;
    flex-direction: column;
    height: fit-content;
    width: 185px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      height: 96px;
      width: 218px;
      background-image: url('../img/advances-line.svg');
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      top: 0;
      left: 0;
      transform: translate(185px, 95%);
    }
    &:nth-child(even) {
      align-self: flex-end;
    }
    &:nth-child(2) {
      &::after {
        transform: rotateX(180deg);
        left: 185px;
      }
    }
    &:nth-child(4) {
      &::after {
        display: none;
      }
    }
  }

  &__item-number {
    font-size: 50px;
    line-height: 60px;
  }
  &__item-text{
    font-size: 18px;
    line-height: 30px;
  }
}

@media(max-width: 1200px) {
  .advances {
    &__item {
      &::after {
        width: 161px;
      }
    }
  }
}

@media(max-width: 1120px) {
  .advances {
    &__item {
      &::after {
        display: none;
      }
      &:nth-child(even) {
        align-self: flex-start;
      }
    }
  }
}

@media(max-width: 990px) {
  .advances {
    &__items {
      flex-wrap: wrap;
      column-gap: 30px;
      justify-content: space-evenly;
      height: auto;
    }
    &__item {
      margin-bottom: 30px;
    }
  }
}