.features {
  padding: 100px 0;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__image-box {
    max-width: 681px;
    border: solid 2px $accent;
    border-radius: 150px 0;
    flex-shrink: 2;
  }
  &__image {
    margin-bottom: 34px;
    margin-left: -34px;
    margin-top: -34px;
    display: block;
    height: 100%;
    object-fit: cover;
  }
  &__text-box{
    flex-shrink: 3;
    max-width: 545px;
    padding-top: 80px;
  }
  &__text-mask {
    @extend %opensans-bold;
    font-size: 288px;
    line-height: 320px;
    letter-spacing: -0.01em;
    background-image: url('../img/features-mask.webp');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-clip: text;
    color: transparent;
  }
}

@media(max-width: 1367px) {
  .features {
    padding: 50px 0;
    &__inner {
      flex-direction: column-reverse;
    }
    &__image-box {
      display: block;
      max-width: 100%;
      border: none;
      border-radius: 166px 0;
      margin-left: 0;
    }
    &__image {
      width: 100%;
      max-width: 100%;
      margin-left: 0;
      margin-top: 0;
      margin-bottom: 0;
      height: 500px;
    }
    &__text-box{
      display: block;
      max-width: 100%;
      padding-top: 50px;
      text-align: center;
    }
  }
}

@media(max-width: 1120px) {
  .features {
    &__image-box {
      display: none;
    }
  }
}

@media(max-width: 769px) {
  .features {
    &__text-box{
      padding-top: 0px;
      
    }
  }
}

@media(max-width: 415px) {
  .features {
    &__text-mask {
      font-size: 188px;
      line-height: 220px;
      letter-spacing: -0.01em;
    }
  }
}