.services {
    position: relative;
    padding: 100px 0;
    &::after {
        content: "";
        position: absolute;
        right: 0;
        bottom: 0;
        background-image: url("../images/icons/bg-dots.svg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        width: 213px;
        height: 154px;
    }
    &__title{
        margin-bottom: 25px;
    }
}

.top-slider {
    max-width: 1360px;
    margin: 0 auto;
    &__items {
        .slick-track {
            height: auto;
            padding-bottom: 15px;
        }
        .slick-next,
        .slick-prev {
            position: absolute;
            top: -62px;
            right: 5px;
            width: 30px;
            height: 30px;
            background-color: $btn-color;
            display: flex;
            justify-content: center;
            align-items: center;
            border: none;
            border-radius: 50%;
            box-shadow: -1px 3px 10px rgba(0, 0, 0, 0.3);
            outline: transparent;
            z-index: 5;
            cursor: pointer;
            path {
                stroke: #ffffff;
            }
        }
        .slick-prev{
            right: 80px;
        }
        .slick-disabled {
            background-color: white;
            box-shadow: -1px 3px 10px rgba(0, 0, 0, 0.3);
            path {
                stroke: $text-color;
            }
        }
        .slick-dots {
            display: flex;
            justify-content: center;
            margin-top: 50px;
            li {
                padding: 0 15px;
                line-height: 0;
                button {
                    cursor: pointer;
                    padding: 0;
                    font-size: 0;
                    background-color: transparent;
                    width: 7px;
                    height: 7px;
                    border-radius: 50%;
                    outline-color: transparent;
                    border: 1px solid transparent;
                    position: relative;
                    &::before {
                        content: '';
                        position: absolute;
                        width: 6px;
                        height: 6px;
                        border-radius: 50%;
                        background-color: $btn-color;
                        opacity: .5;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        transition: all .4s;
                    }
                }
            }    
            .slick-active {
                button {
                    // border-color: red;
                    &::before {
                        opacity: 1;
                        background-color: $btn-color;
                        width: 8px;
                        height: 8px;
                    }
                }
            }
        }
    }
    &__item-wrapper{
        display: grid;
        grid-template-columns: 40px 1fr;
        column-gap: 20px;

    }
    &__item {
        width: 425px;
        padding: 30px 21px 35px 37px;
        box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.14);
        border-radius: 60px 0px;
    }
    &__item-box {
        width: 40px;
        height: 40px;
        
    }
    &__item-img {
        width: 100%;
        height: auto;
        object-fit: cover;
    }
}

.slick-slide{
    margin: 15px;
    
}
.slick-list{
    padding: 0px;
    margin: 0;
}

.item-title{
    @extend %opensans-bold;
    font-size: 24px;
    line-height: 26px;
    color: $title;
    margin-bottom: 15px;
}
.item-text{
    @extend %opensans-regular;
    font-size: 18px;
    line-height: 28px;
    color: $text-color;
    letter-spacing: -0.01em;
}

.--accent-bg .item-text,
.--accent-bg .item-title {
    color: inherit;
}

@media (max-width: 990px) {
    .services {
        padding: 50px 0;
        .slick-dots {
            margin-top: 10px;
        }
    }
}
