.footer {
  padding-top: 90px;
  background-color: $bg-color;
  &__wrapper {
    display: flex;
    justify-content: space-between;
    padding-bottom: 75px;
  }
  &__logo-link {
    margin-bottom: 10px;
    display: block;
  }
  &__logo {
    max-width: 350px;
  }
  &__item-title {
    @extend %opensans-semibold;
    font-size: 24px;
    line-height: 40px;
    color: $title;
    margin-bottom: 20px;
  }
  &__item {
    &+.footer__item {
      margin-bottom: 15px;
    }
  }
  &__social-item {
    display: inline-block;
    &+.footer__social-item {
      margin-left: 8px;
    }
  }
  &__social-link {
    transition: all .6s ease-out;
    path {
      fill: #d5d5d5;
      transition: all .6s ease-out;
    }
  }
  &__social-item:hover .footer__social-link {
    svg {
      transform: scale(1.1);
    }
    path {
      fill: $btn-color;
    }
  }
  &__copyright {
    font-size: 18px;
    text-align: center;
    padding: 22px 0 22px;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      height: 1px;
      width: 100%;
      background-color: #c4c4c4;
      top: 0;
      left: 0;
      right: 0;
    }
  }
  &__copyright-link {
    color: $btn-color;
  }
}
.column--width {
  width: 200px;
}

@media (max-width: 1367px) {
  .footer {
    &__logo {
      max-width: 284px;
    }
  }
}

@media (max-width: 1200px) {
  .footer {
    &__logo {
      max-width: 215px;
    }
    &__wrapper {
      flex-wrap: wrap;
      column-gap: 50px;
      row-gap: 30px;
      justify-content: space-evenly;
    }
  }
}
@media (max-width: 770px) {
  .footer {
    &__logo {
      display: none;
    }
  }
}
@media (max-width: 640px) {
  .footer {
    &__wrapper {
      flex-direction: column;
      margin-left: 50px;
    }
    &__copyright {
      padding: 22px;
    }
  }
}