
.about {
  padding: 100px 0;
  &__inner{
    display: flex;
    justify-content: space-between;
    
  }
  &__image-box {
    max-width: 681px;
    border: solid 2px $accent;
    border-radius: 150px 0;
    flex-shrink: 2;
    // margin-left: 100px;
  }
  &__image {
    border-radius: 148px 0;
    margin-bottom: -34px;
    margin-left: 34px;
    margin-top: 34px;
    display: block;
    height: 100%;
    object-fit: cover;
  }
  &__text-box{
    flex-shrink: 3;
    max-width: 545px;
    padding-top: 80px;
  }
  &__title{
    margin-bottom: 22px;
    
  }
  &__text{
    margin-bottom: 40px;
  }
}

@media (max-width: 1367px) {
  .about {
    padding: 50px 0;
    &__inner {
      flex-direction: column;
    }
    &__image-box {
      display: block;
      max-width: 100%;
      border: none;
      border-radius: 0;
      flex-shrink: 1;
    }
    &__image {
      width: 100%;
      max-width: 100%;
      margin-left: 0;
      margin-top: 0;
      margin-bottom: 0;
      height: 500px;
      border-radius: 0;
    }
    &__text-box{
      display: block;
      max-width: 100%;
    }
  }
}
@media (max-width: 415px) {
  .about {
    padding: 50px 0;
    &__image {
      height: 330px;
    }
  }
}
@media (max-width: 376px) {
  .about {
    &__text {
      margin-bottom: 18px;
    }
  }
}