.subscription {
  padding: 200px 0 200px;
  &__title {
    text-align: center;
    text-transform: uppercase;
    padding-bottom: 37px;
    margin-bottom: 40px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      height: 1px;
      width: 461px;
      background-color: #C4C4C4;
      bottom: 0;
      left: 0;
      transform: translateX(100%);
    }
  }
  &__text {
    line-height: 28px;
    text-align: center;
    margin-bottom: 33px;
  }
  &__form {
    text-align: center;
  }
  &__input {
    padding: 22px 30px 22px 30px;
    border: none;
    background-color: #cccccc;
    border-radius: 20px 0 0 0;
    @extend %raleway-medium;
    font-size: 18px;
    line-height: 20px;
    color: $text-color;
    outline: none;
    width: 592px;
  }
  &__button {
    padding: 22px 42px 24px; 
    border: none;
    background-color: $btn-color;
    margin-left: -4px;
    border-radius: 0 0 20px 0;
    color: #ffffff;
    text-transform: uppercase;
    @extend %opensans-semibold;
    font-size: 18px;
    line-height: 100%;
    cursor: pointer;
  }
}

@media(max-width: 1367px) {
  .subscription {
    &__title {
      &::after {
        display: none;
      }
    }
  }
}

@media(max-width: 990px) {
  .subscription {
    padding: 100px 0;
    &__input {
      width: calc(100% - 180px);
    }
  }
}

@media(max-width: 640px) {
  .subscription {
    padding: 50px 0;
    &__input {
      display: block;
      max-width: 100%;
      width: 100%;
      margin-bottom: 30px;
      border-radius: 50px;
    }
    &__button {
      border-radius: 50px;
    }
  }
}